.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  margin: 0 0 60px;
  padding: 0;
  border-top: 2px solid #EC3E5A;
}

.react-tabs__tab {
  display: inline-block;
  border-bottom: none;
  position: relative;
  list-style: none;
  padding: 15px 40px;
  cursor: pointer;
  color: #282830;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.01em;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
}

.react-tabs__tab--selected {
  background: #EC3E5A;
  color: #fff;
}

.react-tabs__tab--disabled {
  color: #282830;
  cursor: default;
}

.react-tabs__tab:focus {
  box-shadow: 0 0 5px hsl(208, 99%, 50%);
  border-color: hsl(208, 99%, 50%);
  outline: none;
}

.react-tabs__tab:focus:after {
  content: "";
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}
